.categories {
  margin-bottom: 20px;
}
.categories > .btn {
  display: none;
}
.categories .list-group.dropdown-menu {
  position: static;
  float: none;
  border: 0;
}
.categories li a {
  border-bottom: 2px solid transparent;
  color: #333d47;
}
.categories li a:hover {
  color: #f6323e;
  text-decoration: none;
}
.categories .active {
  border-color: #f6323e;
  color: #f6323e;
}

@media (max-width: 576px) {
  .categories > .btn {
    display: inline-block;
    width: 100%;
    color: #fff;
    background-color: #7290ff;
    cursor: pointer;
  }
  .categories .list-group.dropdown-menu {
    position: absolute;
    float: left;
    display: none;
  }
  .categories.active .list-group.dropdown-menu {
    display: block;
    width: 100%;
  }
  .categories.active > .btn {
    background: #4968d8;
  }
  .categories.active > .btn.dropdown-toggle::after {
    border-top: 0;
    border-bottom: 0.3em solid;
  }
}

.products--list {
  list-style: none;
}

.products--list li {
  margin-bottom: 10px;
  cursor: pointer;
}
.products--list .card-header {
  border-bottom: 0;
}
.products--list .card-body {
  border-top: 1px solid rgba(0, 0, 0, 0.125);
}

.form-group {
  margin: 0;
}
.form-control {
  color: #fff;
  border-color: #fff;
  background: transparent;
}
.form-control::-webkit-input-placeholder {
  color: #fff;
}
.form-control::-moz-placeholder {
  color: #fff;
}
.form-control::-ms-input-placeholder {
  color: #fff;
}
.form-control::placeholder {
  color: #fff;
}

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 9999;
  background: #f6323e;
  color: #fff;
  padding: 5px 0;
}

h1 {
  margin: 0;
  padding: 0;
}
.content {
  margin: 50px auto;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}
.app {
  width: 100%;
}

html {
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

button:disabled {
  opacity: 0.5;
}

